.container {
    display: flex;
    margin: 4rem 10rem 2rem 8rem ;
    padding-bottom: 3rem;
    
}

.container-img {
    width: 400px;
    height: 380px;
    padding-top: 6.5rem;

}

.descr-one{
    margin-left: 6.5rem;
}
.descr-one h1{
 font-size: 2.8rem;
}
.descr-one p {
    font-size: 1.2rem;
    font-weight: 350;
    margin-right: 8rem;
    padding-top: 1.5rem;
   }

   .meeting-schedule {
    padding: 0.5rem;
    margin-top: 2rem;
    background-color: transparent;
    font-weight: 300;
    font-size: 1.2rem;
    border-radius: 16px;
    color: white;
   }

   .meeting-schedule:hover{
    background: rgba(10, 59, 14, 0.6);
    border: 1px rgb(255, 250, 250) solid;
    cursor: pointer;
    transition: ease-in 0.2s;
   }